<template>
    <!--  -->
    <div class="content">
        <div class="select-box">
            <span>:</span>
            <div class="changeLeft" v-bind:class="{ changeLeftActive: isActive }">            
                <el-select v-model="value1"  placeholder="" @change="sourceLanguageChange(value1)">
                    <el-option
                    v-for="item in languageList1"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                    </el-option>
                </el-select>
            </div>
            <span class="iconfont icon-qiehuan changemiddle" @click="transChange"></span>
            <div class="changeRight" v-bind:class="{ changeRightActive: isActive }">
                <el-select v-model="value2"  placeholder="" @change="targetLanguageChange(value2)">
                    <el-option
                    v-for="item in languageList2"
                    :key="item.value"
                    :label="item.label"
                    :disabled=true
                    :value="item.value">
                    </el-option>
                </el-select>   
            </div>
            <!-- <el-button type="primary" @click="translateBtn"></el-button> -->
        </div>
        
        <div class="translate">
            <div class="translate-left">
                <el-input
                    type="textarea"
                    :rows="10"
                    resize='none'
                    placeholder=""
                    v-model="textarea1">
                </el-input>
            </div>

            <div class="translate-right">
                <el-input
                    type="textarea"
                    :rows="10"
                    resize='none'
                    placeholder=""
                    v-model="textarea2">
                </el-input>
            </div>
        </div>

        <div class="speak-img-box" @click="startPlay" v-if="!readyOriginalShow">
            <img src="../assets/img/startSpeak.png" alt="" srcset="">
            <div></div>
        </div>

        <!--  -->
        <div class="vioce-speak speak-img-box" v-if="readyOriginalShow" @click="stopVioce">
            <img class="voiceprintImg" src="../assets/img/voiceprintLeft.jpg" alt="" srcset="">
            <img src="../assets/img/endSpeak.png" alt="" srcset="">
            <img class="voiceprintImg" src="../assets/img/voiceprintRight.jpg" alt="" srcset="">
            <div></div>
        </div> 

        <div class="footer">
            <div></div>
            <div></div>
            <div></div>
        </div>
    </div>
</template>
<script>
import qs from 'qs';
import Recorder from 'recorder-core';
export default {
    name:'textTranslation',
    data(){
        return{
            languageList1: [{
                value: 'zh-cmn-Hans-CN',
                label: ''
                }, 
            ],
            value1: 'zh-cmn-Hans-CN',
            languageList2: [{
                value: 'en-US',
                label: ''
                },],
            value2: 'en-US',
            textarea1:'',
            textarea2:'',
            readyOriginalShow:false,
            audioSrc:'',
            isActive:false,
            realTimeSendTryTime:0,
            realTimeSendTryType:'mp3',
            realTimeSendTryEncBusy:null,
            realTimeSendTryNumber:null,
            transferUploadNumberMax:null,
            realTimeSendTryChunk:null,
            testSampleRate:16000,
            testBitRate:16,
            SendInterval:300,
            testOutputWavLog:false,
        }
    },

    methods:{
        //
        sourceLanguageChange(val){
            console.log(val+'');
            //TODO 
        },
       
         //
        transChange(){
            console.log('');
            this.isActive = !this.isActive;
            let changeVal = this.value1;
            this.value1 = this.value2;
            this.value2 = changeVal;
        },

         //
        startPlay(){
            this.readyOriginalShow = true;
            let that=this;
            //console.log('');
            //console.log(Recorder);
            that.recBlob=null;//
            that.rec=Recorder({
                type:"wav",sampleRate:16000,bitRate:16
                ,onProcess:function(buffers,powerLevel,bufferDuration,bufferSampleRate){
                   //console.log(buffers);
                   that.RealTimeSendTry( that.rec, false);
                }
            });
            //console.log("...");
            //TODO 
            that.rec.open(function(){
                console.log("",2);
                that.rec.start();
                //TODO 
                that.initWebSocket();
            },function(msg,isUserNotAllow){//
                that.rec=null;
                //TODO  
                //console.log((isUserNotAllow?"UserNotAllow":"")+":"+msg, 1);
            });    
           
        },

        //,
        RealTimeSendTry(rec,isClose){
            let that = this;
            var t1=Date.now(),endT=0,recImpl=Recorder.prototype;
            //console.log(t1);
            if(that.logrealTimeSendTryTime==0){
                that.realTimeSendTryTime=t1;
                that.realTimeSendTryEncBusy=0;
                that.realTimeSendTryNumber=0;
                that.transferUploadNumberMax=0;
                that.realTimeSendTryChunk=null;
            };
            if(!isClose && t1-that.realTimeSendTryTime<that.SendInterval){
                return;//
            };
            that.realTimeSendTryTime=t1;
            var number=++that.realTimeSendTryNumber;
            
            //SampleData
            var chunk=Recorder.SampleData(rec.buffers,rec.srcSampleRate,that.testSampleRate,that.realTimeSendTryChunk,{
                    frameType:isClose?"":that.realTimeSendTryType
                });
           // console.log(chunk);
            //stop
            for(var i=that.realTimeSendTryChunk?that.realTimeSendTryChunk.index:0;i<chunk.index;i++){
                rec.buffers[i]=null;
            };
            that.realTimeSendTryChunk=chunk;
            
            //mock
            //console.log(chunk.data);
           
            if(chunk.data.length==0 || (isClose&&chunk.data.length<2000)){
                //console.log('')
                that.TransferUpload(number,null,0,null,isClose);
                return;
            };

            //
            if(!isClose){
                if(that.realTimeSendTryEncBusy>=2){
                   //console.log("",1);
                    return;
                };
            };
            that.realTimeSendTryEncBusy++;
            
            //mockmp3wav
            var encStartTime=Date.now();
            var recMock=Recorder({
                type:that.realTimeSendTryType
                ,sampleRate:that.testSampleRate //
                ,bitRate:that.testBitRate //
            });
            //console.log('88888')
            recMock.mock(chunk.data,chunk.sampleRate);
            recMock.stop(function(blob,duration){
                that.realTimeSendTryEncBusy&&(that.realTimeSendTryEncBusy--);
                blob.encTime=Date.now()-encStartTime;
                //
                that.TransferUpload(number,blob,duration,recMock,isClose);
            },function(msg){
                that.realTimeSendTryEncBusy&&(that.realTimeSendTryEncBusy--);
                
                //
               //console.log(":"+msg,1);
            });
        },

        //
        TransferUpload(number,blobOrNull,duration,blobRec,isClose){
            let that = this;
            that.transferUploadNumberMax=Math.max(that.transferUploadNumberMax,number);
            if(blobOrNull){
                var blob=blobOrNull;
                var encTime=blob.encTime;
                
                //*********Read As Base64***************
                var reader=new FileReader();
                reader.onloadend=function(){
                    var base64=(/.+;\s*base64\s*,\s*(.+)$/i.exec(reader.result)||[])[1];
                    //console.log(base64);
                    //console.log(reader.result);
                    that.websocketsend(base64);
                    //
                    //WebSocket send(base64) ...
                    //WebRTC send(base64) ...
                    //XMLHttpRequest send(base64) ...
                    
                    //
                };
                reader.readAsDataURL(blob);
                //console.log('0000');
                //console.log(blob)
                //*********Blob***************
                //
                //that.websocketsend(blob);
                //WebRTC send(blob) ...
                //XMLHttpRequest send(blob) ...
                
                // console send 
                var numberFail=number<that.transferUploadNumberMax?'<span style="color:red">SendInterval</span>':"";
                var logMsg="No."+(number<100?("000"+number).substr(-3):number)+numberFail;
                
            };
            
            if(isClose){
                //console.log("No."+(number<100?("000"+number).substr(-3):number)+":");
            };
        },

        //weosocket
        initWebSocket(){ 
            const wsuri = "ws://xz502.tpddns.cn:7766";
            this.websock = new WebSocket(wsuri);
            this.websock.onmessage = this.websocketonmessage;
            this.websock.onopen = this.websocketonopen;
            this.websock.onerror = this.websocketonerror;
            //this.websock.onclose = this.websocketclose;
        },

        //send
        websocketonopen(){
            let promise = {
                "header":{
            "namespace":"SpeechInterpreter",
            "name":"StartInterpretation",
            "task_id":"0220a729ac9d4c9997f51592ecc83847"
            },
            "payload":{
            "source_lan":this.value1,
            "target_lan":this.value2,
            "format":"pcm",
            "sample_rate":16000,
            "enable_intermediate_result":true,
            "enable_punctuation_prediction":true,
            "enable_inverse_text_normalization":true,
            "max_sentence_silence":800,
            "enable_words":true,
            }
            };

            this.websocketsend(JSON.stringify(promise));
        },

        websocketonerror(){//
            console.log('');
            this.initWebSocket();
        },

        //
        websocketonmessage(e){ 
            let that = this;
            const redata = JSON.parse(e.data);
            console.log(redata);
            if( redata.header.status == "00000"){
                //that.initWebSocket();
                //that.recBlob=null;//
                if(that.websock.readyState && that.websock.readyState == '1'){
                    that.websock.binaryType = 'arraybuffer'; // ArrayBuffer 
                    that.RealTimeSendTry( that.rec, false);
                }
                that.textarea1 = redata.payload.result;
                that.textarea2 = redata.payload.transcript;
            }else{
                 that.websock.close();
                 that.rec.close();
            }
        },

        //
        websocketsend(Data){
            let that = this;
            console.log(Data);
            console.log(that.websock.readyState);
            if(that.websock.readyState == '1'){
                that.websock.send(Data);    
            }else{
                that.websock.close();
                that.rec.close();
                //that.initWebSocket();
            };
        },

        // 
        websocketclose(e){  
            console.log('',e);
        },

        //
        stopVioce(){
            this.websock.close();
            this.rec.close();
            this.readyOriginalShow = false;
        }
    }
}
</script>
<style lang="css" scoped>
.content{
    height: 100%;
    padding: 0 32px;
    box-sizing: border-box;
    position: relative;
}
.select-box{
    text-align: left;
    padding-top: 23px;
    position: relative;
    line-height: 40px;
}
.select-box button{
    width: 89px;
    background: #2F5BEA;
    margin-left: 23px;
    position: absolute;
    left: 416px;
    right: 0;
}
.icon-qiehuan{
    margin: 0 12px;
}
.translate{
    width: 100%;
    display: flex;
}
.translate div{
    flex: 1;
    margin-top: 18px;
}
.translate-left{
    margin-right: 50px;
}
.speak-img-box{
    position: absolute;
    margin-top: 12px;
    font-family: PingFangSC-Regular;
    font-size: 14px;
    color: rgba(0,0,0,0.85);
    line-height: 22px;
    left: 5%;
    top: 36%;
}

.footer{
    width: 100%;
    background: #E9EEF3;
    position: absolute;
    bottom: 0;
    left: 0;
    padding-top: 24px;
    box-sizing: border-box;
}
.footer div{
    display: inline;
    margin: 0 40px;
}
.changeLeft{
    position: absolute;
    left: 80px;
    top: 25px;
    width: 150px;
}
.changemiddle{
    position: absolute;
    left: 226px;
    top: 25px;
    width: 20px;
}
.changeRight{
    width: 150px;
    position: absolute;
    left: 260px;
    top: 25px;
}
.changeLeftActive{
    left: 260px;
}
.changeRightActive{
    left: 80px;
}
.translate-right textarea{
    background-color: #fff !important;
}
</style>
