<template>
    <!--  -->
    <div class="content-box">
        <div class="identification-box">
           <div class="identification-type">
                <label for="value" class='language-label'>  :&nbsp;&nbsp;</label>
                <el-select v-model="value" placeholder="" >
                    <el-option
                    v-for="item in languageList"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                    </el-option>
                </el-select>
            </div> 
            <div class="identification-button">
                <el-button type="primary" @click="registration"></el-button>
                <el-button  @click="dialogTableVisible = true"></el-button>
            </div> 
        </div>
        
        <div v-if="value == '1'">
            <div class="vioceInput">
                <span>&nbsp;&nbsp;</span>
                <el-input
                    placeholder=""
                    v-model="vioceName"
                    maxLength='10'
                    clearable>
                </el-input>
            </div>  

            <div class="youCanspeak-eg">
            
            </div>

            <div class="speak-img-box" @click="oneOnOne" v-if="!oneOnOneShow">
                <img src="../assets/img/startSpeak.png" alt="" srcset="">
                <div>{{voiceText}}</div>
            </div>
            <!--  -->
            <div class="vioce-speak speak-img-box" v-if="oneOnOneShow">
                <img class="voiceprintImg" src="../assets/img/voiceprintLeft.jpg" alt="" srcset="">
                <img src="../assets/img/endSpeak.png" alt="" srcset="">
                <img class="voiceprintImg" src="../assets/img/voiceprintRight.jpg" alt="" srcset="">
                <div>{{time}}s</div>
            </div> 

            <div class="System-identification-results">
                    <span class="el-icon-success" style="color:green;margin-right:5px" v-if="iconSuccess"></span> <span   v-if="iconError" class="el-icon-error" style="color:red;margin-right:5px"></span><span>{{VoiceResult}}</span>
            </div>

        </div>

         <div v-else-if ="value == '2'">
            <div class="One-to-many-text-tip">
                
            </div>
            <div class="speak-img-box" @click="readyOriginal" v-if="!readyOriginalShow">
                    <img src="../assets/img/startSpeak.png" alt="" srcset="">
                    <div>{{voiceText}}</div>
            </div>
            <!--  -->
            <div class="vioce-speak speak-img-box" v-if="readyOriginalShow">
                <img class="voiceprintImg" src="../assets/img/voiceprintLeft.jpg" alt="" srcset="">
                <img src="../assets/img/endSpeak.png" alt="" srcset="">
                <img class="voiceprintImg" src="../assets/img/voiceprintRight.jpg" alt="" srcset="">
                <div>{{time}}s</div>
            </div> 
            <div class="One-to-many-result">
                <span>{{readyOriginalResult}}</span>
            </div>
        </div>
        <!--  -->
        <el-dialog title="" :visible.sync="dialogTableVisible" width='500px'>
            <div class="search-box">
                <el-input placeholder="" v-model="input3" class="input-with-select" style="border-right:none">
                    <el-button slot="append" icon="el-icon-search" style="background-color:#fff"></el-button>
                </el-input>
            </div>
            <div class="select-box">
                <div class="selecct-left">
                    <el-checkbox :indeterminate="isIndeterminate" v-model="checkAll" @change="handleCheckAllChange" ></el-checkbox>
                        <el-checkbox-group v-model="checkedCities" @change="handleCheckedCitiesChange">
                        <el-checkbox v-for="city in cities" :label="city" :key="city">{{city}}</el-checkbox>
                    </el-checkbox-group>     
                </div>
                <div class="select-right">
                    <div class="select-right-title"></div>
                    <div v-for="item in timeList" :key="item">{{item}}</div>
                </div>
            </div>
            <div class="submit-btn-box">
                <el-button @click="cancellationBtn"></el-button>
                <el-button type="primary" @click="dialogTableVisible=false"></el-button>
            </div>
        </el-dialog>   
    </div>
</template>
<script>
import Recorder from 'recorder-core'
export default {
  name: 'vioceSee',
  props: {
    msg: String
  },
    data() {
      return {
        activeName: 'second',
        languageList: [{
          value: '1',
          label: ''
        }, {
          value: '2',
          label: ''
        }],
        isVoice: false,
        isFinished: false,
        tipMsg: '',
        VoiceResult:'',
        audio: "",
        value: '1',
        vioceName: '',
        dialogTableVisible:false,
        input3:'',
        checkAll: false,
        checkedCities: ['', ''],
        cities: ['', '', '', '',''],
        isIndeterminate: true,
        timeList:['2020-06-19 20:30:34','2020-06-19 20:30:35','2020-06-19 20:30:37','2020-06-19 20:30:38','2020-06-19 20:30:39'],
        iconSuccess:false,
        iconError:false,
        oneOnOneShow:false,
        readyOriginalShow:false,
        time:8,
        time1:null,
        voiceText:'',
        readyOriginalResult:'',
      };
    },
    mounted(){
        //this.axios.defaults.baseURL = '/user'  //
        console.log(this.axios.defaults.baseURL);
    },
    methods: {

        handleClick(tab, event) {
            console.log(tab, event);
        },
        
        //
        registration(){
            this.$router.push({path:'/VoiceprintRegistration'})
        },

        //
        handleCheckAllChange(val) {
            this.checkedCities = val ? this.cities : [];
            this.isIndeterminate = false;
        },

        //
        handleCheckedCitiesChange(value) {
            let checkedCount = value.length;
            this.checkAll = checkedCount === this.cities.length;
            this.isIndeterminate = checkedCount > 0 && checkedCount < this.cities.length;
        },

        //
        oneOnOne(){
            if(this.vioceName ==''){
                this.$notify({
                    title: '',
                    message: '',
                    type: 'warning'
                });
            }else{
                this.iconSuccess=false;
                this.iconError = false;
                this.oneOnOneShow= true;
                this.vioceStart();    
            }
        },

        //
        readyOriginal(){
            this.iconSuccess=false;
            this.iconError = false;
            this.readyOriginalShow = true;
            this.vioceStart();
        },

        //
        vioceStart(){
            if(this.time1){
               clearInterval(this.time1);     
            };
            let that=this;
            console.log('');
            console.log(Recorder);
            this.recBlob=null;//
            this.rec=Recorder({
                type:"wav",sampleRate:16000,bitRate:16
                ,onProcess:function(buffers,powerLevel,bufferDuration,bufferSampleRate){
                    //console.log(arguments);
                }
            });
            console.log("...");
           
            //TODO 
            this.rec.open(function(){
                console.log("",2);
                that.rec.start();
                //TODO 
                that.time1= setInterval(() => {
                    if(that.time<=0 ){
                     that.time =0;           
                    }else{
                        that.time--;
                    }
                }, 800);
                that.VoiceResult = '...';
                that.readyOriginalResult = '...';
                setTimeout(function(){ 
                    that.vioceEnd();
                }, 8000);
            },function(msg,isUserNotAllow){//
                that.rec=null;
                //TODO  
                console.log((isUserNotAllow?"UserNotAllow":"")+":"+msg, 1);
            });   
        },

         //
        vioceEnd(){
            let that  = this;
            if(!that.rec){
                console.error("");
                //TODO 
                return;
            }
            this.rec.stop(function(blob,duration){
                that.recBlob=blob;
                that.rec.close();//startclose
                console.log(blob,duration,that.rec);
                that.rec=null;
                
            },function(msg){ 
                console.log(":"+msg, 1);
                this.rec.close();//stop3close
                this.rec=null;
            });

            setTimeout(function(){that.upload()}, 3000);
        },

         //
        upload(){
            var that=this;
            var blob=that.recBlob;
            if(!blob){
                console.error("");
                //TODO 
                return;
            };
            that.VoiceResult = '...';
            var reader=new FileReader();
            reader.onloadend=function(){
                var base64=(/.+;\s*base64\s*,\s*(.+)$/i.exec(reader.result)||[])[1];
                if(that.vioceName == ''){
                    var promise = {
                        'appId':'huiyandemo',
                        'data': base64,
                        'format':'wav',
                        'sampleRate': 16000,
                    }
                }else{
                    var promise = {
                        'appId':'huiyandemo',
                        'data': base64,
                        'userName':that.vioceName,
                        'format':'wav',
                        'sampleRate': 16000,
                    }
                }
                that.axios.post('http://xz502.tpddns.cn:25003/verify', promise)
                .then(function (response) {
                    console.log(response);
                    if(response.status == '200'){
                        console.log(response.data);
                        that.voiceText='';
                        that.readyOriginalShow = false;
                        if(response.data.status == '22102'){
                            //TODO 
                            that.VoiceResult = '';
                            that.readyOriginalResult = response.data.userName;
                            that.iconSuccess = true;
                            that.oneOnOneShow= false;
                            that.$notify({
                                title: '',
                                message: response.data.message,
                                type: 'success'
                            });
                        }else{
                            that.readyOriginalResult = ''
                            that.VoiceResult = '';
                            that.oneOnOneShow = false;
                            that.$notify.error({
                                title: '',
                                message: response.data.message
                            } );
                            that.iconError = true;  
                        }
                    }else{
                       that.$notify.error({
                            title: '',
                            message: ',!'
                        } );
                    }
                })
                .catch(function (error) {
                    console.log(error);
                });
            };
            reader.readAsDataURL(blob);
        },

        //
        cancellationBtn(){
            console.log('');
            let that = this;
            //TODO 
            let promise = {
                    'appId':'huiyandemo',
                    'userName':'wujie001'
            };
            that.axios.post('http://xz502.tpddns.cn:25003/unregister', promise).then(function(response){
                  if(response.status == '200'){
                       console.log(response.data);
                        if(response.data.status == '22104'){
                            that.$notify({
                                    title: '',
                                    message: response.data.message,
                                    type: 'success'
                            });  
                        }else{
                            that.$notify.error({
                                title: '',
                                message: response.data.message
                            } );    
                        }
                  }else{
                       that.$notify.error({
                            title: '',
                            message: ',!'
                        } );
                  }
                  that.dialogTableVisible = false;
            })

        },
    }
}
</script>
