    .identification-button{
        flex: 1;
        text-align: right;
        padding-top: 23px;
    }
     .vioceInput .el-input{
        width: 346px;
        margin-top: 65px;
    }
    .vioceInput .el-input input{
        width: 346px;
    }
    .vioceInput span{
      
       font-family: PingFangSC-Regular;
        font-size: 14px;
        color: rgba(0,0,0,0.85);
        line-height: 22px;
    }
    .youCanspeak-eg{
        font-family: PingFangSC-Regular;
        font-size: 14px;
        color: rgba(0,0,0,0.65);
        line-height: 22px;
        margin-top: 33px;
    }
    .speak-img-box{
        margin-top: 70px;
    }
    .speak-img-box div{
        margin-top: 9px;
        font-family: PingFangSC-Regular;
        font-size: 14px;
        color: rgba(0,0,0,0.85);
        line-height: 22px;
    }
    .System-identification-results{
        margin-top: 73px;

    }
    .System-identification-results span{
        font-family: PingFangSC-Regular;
        font-size: 14px;
        color: #989898;
        line-height: 22px;
    }
    .One-to-many-text-tip{
        width: 472px;
        font-family: PingFangSC-Medium;
        font-size: 14px;
        color: #000000;
        line-height: 22px;
        text-align: left;
        margin: 0 auto;
        margin-top: 59px;
    }
    .speak-img-box{
        margin-top: 63px;
    }
    .One-to-many-result{
        text-align: left;
        margin-left: 308px;
