        color: rgba(0,0,0,0.65);
        line-height: 22px;
    }
    .search-box{
        width: 426px;
        margin: 0  auto;
    }
    .select-box{
        display: flex;
        width: 426px;
        height: 290px;
        margin: 0 auto;
        margin-top: 13px;
        border: 1px solid rgba(0,0,0,0.15);
        border-radius: 5px;
        overflow-y: scroll;
    }

    .select-box .selecct-left{
        flex:1;
